.menu{
    text-decoration: none;
    box-sizing: border-box;
    font-family: 'Open Sans', cursive;
}
.mdiv{
    border-radius: 20%;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    text-align: center;
    vertical-align: middle;
    padding: 5px;
    font-family: 'Gupter', serif;
    box-sizing: border-box;
}
.selectedMenu{
    color: lightgreen;
    text-decoration: none;
    box-sizing: border-box;
}
.vmenuLayout{
    display: block;
    padding:10px;
    border-bottom: 1px solid green  ;
    text-align: center;
    
    background-color: white;
    width: 150px;
    box-sizing: border-box;
}
.hmenu{
    color:white;
}
.vmenu{
    color:green;
}
