.compDiv{
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    box-sizing: border-box;
}
.tbxNbtn{
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    box-sizing: border-box;
    width:90%;
    height: 100%;
    margin-right: 0px;
}

.tbx{
   height: 100%;
   box-sizing: border-box;
   border-width:1px;
   border-style: solid; 
   width:90%;
   display: flex;
}
.imgBtnDiv{
    height: 100%; 
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    box-sizing: border-box;
}
.dd{

    box-sizing: border-box;
    height: 100%;
    width:40%;
    margin-right: 0px;
    display:flex;
}
.tbxDD{
    height: 100%;
    box-sizing: border-box;
    border-width:1px;
    border-style: solid; 
    width:50%;
    background-color: white;
    margin-right: 0px;
    display: flex;
 }
 .imgBtnDivDD{
    height: 100%; 
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    box-sizing: border-box; 
    margin-left: 0px;
}

@media screen and (max-width: 480px) {
   .compDiv{
        display: block;
        position: relative;
        width:100%;
    } 
    
    .dd{
        display: block;
        width:80%;
    }
    
    .tbxDD{
       
    }  
  }
