.mainImgDiv{
    position: relative;
    display:inline;
    height: 100%;
}
.svgDiv{
    position: absolute;
    left:0px;
    top:0px;
    height: 100%;
    border-width:1px;
    border-style: solid;
   box-sizing: border-box;
   border-top-right-radius: 10%;
   border-bottom-right-radius: 10%; background-color: white;
}
.svgDiv:hover{
    cursor: pointer;
}