.main{
    display: flex;
    flex-direction: column;
    position: relative;
    padding:20px;
    margin:auto;
    box-sizing: border-box;
    background-color: green;
    height: auto;
    border-radius: 10px;
}
.content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    margin:auto;
    box-sizing: border-box;
    background-color: white;
}
.video{
    display: none;
    position: relative;
    box-sizing: border-box;
}
.imgGal{
    display: block;
    width: 90%;
    position: relative;
    box-sizing: border-box;
}
.img{
    display: flex;
    width:100%;
}
.btns{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
    position: relative;
    margin: 10px;
}
.btn{
    height: 30px;
    background-color: white;
    color: green;
    border-radius:50%;
    box-sizing: border-box;
    border: 1px solid green;
}
.desc{
    color:green;
    margin:10px;
}

