.rightPos{
    position: absolute;
    top:10px;
    right: 20px;
    display:flex;
    flex-direction: row;
    text-align: center;  
    box-sizing: border-box; 
}
.vmenus{
    margin-left: 40px;
    background-color:white ;
    border:1px solid green;
    border-radius: 10px;
    width:150px;
    position: absolute;
    top:35px;
    z-index:78;
    box-sizing: border-box;
    
}

.bringMenusIn{
     /* animation */
     animation:  comeFromLeft 0.5s;
}
.vmenusContainer{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    width:50px;
    position: relative;  
    box-sizing: border-box;
}

.vmenusBtn{
    display:flex;
    flex-direction: column;
    margin:5px;
    padding:5px;
    width:50px;
    color:white;
    z-index:77; 
    box-sizing: border-box;
}
.vmenusBtn:hover{
    background-color: #31708E;
}
.menuItem{
    background-color: white;
    margin: 2px;
    width:30px;
    height: 3px;
    box-sizing: border-box; 
}


@keyframes comeFromLeft{
    0%{
         transform: translateX(-20%);
    }
    100%{
        transform: translateX(0%);
    }
}
@keyframes moveBack{
    0%{
         transform: translateX(0%);
    }
    100%{
        transform: translateX(-100%);
    }
}
