.content{
    width:400px;
    position: relative;
    margin:auto;
    padding:30px;
    height: 500px;
}
.question{
    display:block;
    color:white;
    font-family: 'Open Sans',serif;
    font-size: 1.5em;
    margin:10px 0px;
}
.lab{
    color:white;
    text-align: left;
}
.addPicDiv{
    display: flex;
    flex-direction: row;
    width:100%;
    color: white;
    height: 30px;
    margin:5px 0px;
    box-sizing: border-box;
}

.hidden{
    display: none;
}
.inp{
    width:100%;
    color:green;
    height: 30px;
    box-sizing: border-box;
}
.textarea{
    width:100%;
    color:green;
    height: 100px;
    box-sizing: border-box;
}
form{
    position: relative;
    box-sizing: border-box;
}
form input,button,textarea{
    display: block;
    width:100%;
    box-sizing: border-box;
}

@media screen and (max-width:480px)
{
    .content{
        width:100%;
        display: flex;
        flex-direction: column;
        padding:10px;
        box-sizing: border-box;
    }
    .addPicDiv{
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: flex-start;
        height: auto;

    }
}