.mainDiv{
    display:flex;
    flex-direction: row;
    width:100%;
    padding-top: 10px;
    z-index: -1; 

}
.imgDiv{
   display:flex;
}
.adimg{
    display: flex;
    width:100%;
  
}
.textDiv{
    display: flex;
    flex-direction: row;
    z-index: 1;
    color:green;
    position: absolute;
    left:0px;top:100px;
    width:100%;
}

@keyframes comeUp{
    0%{transform: translateY(20%)}
    100%{transform: translateY(0%)}
}
@keyframes moveDown{
    0%{transform: translateY(-20%)}
    100%{transform: translateY(0%)}
}
@keyframes fromLeft{
    0%{transform: translateX(-20%)}
    100%{transform: translateX(0%)}
}
@keyframes fromRight{
    0%{transform: translateX(20%)}
    100%{transform: translateX(0%)}
}

.animUp{
    animation: comeUp 0.5s ease; 
}
.animDown{
    animation: moveDown 0.5s ease; 
}
.animLeft{
    animation:fromLeft 0.5s, ease ;
}
.animRight{
    animation: fromRight 0.5s ease; 
}