.mainDiv{
    display:flex;
    flex-direction:column;
    justify-content:space-evenly;
    padding:5px;
    width:200px;
    height:220px;
    background-color: white;
    position: relative; 
    box-sizing: border-box;
    margin: 3px;
    border:0.5px solid lightgreen ; 
     
}
.mainDiv:hover{
    cursor: pointer;
}
.imgDiv{
    display: flex;
    width: 100%;
    box-sizing: border-box;
}
.img{
    display: flex;
    width: 100%;
    height: auto;
    box-sizing: border-box;
}

.disc{
    position: absolute;
    top:5px;
    right:5px;
    background-color:white;
    color:orange;
    border:1px solid orange;
    padding:3px;
    box-sizing: border-box;
}
.desc{
    margin-top:5px;
    display: block;
    width:100%;
    text-align: center;
    box-sizing: border-box; 
    text-align:left; 
    color:rgb(64,64,64);
}
.rating{
    margin-top: 5px;
    display: block;
    width:100%;
    text-align: center;
    box-sizing: border-box;
    color:orange;
    text-align:left; 
}
.price{
    margin-top: 5px;
    display: block;
    width:100%;
    height: auto;
    text-align: center;
    box-sizing: border-box;
    font-weight: bold;
    text-align:left; 
    color: black;
}
.navlink{
    text-decoration: none;
}

@media screen and (max-width:480px)
{
  .mainDiv{
        width:100%; 
        height: auto;
       /* margin-bottom: 10px; */
    } 
}
