.mainDiv{
    height: 150px;
    background-color: green;
    box-sizing: border-box; 
    width:100%;
    position: relative;
}
.headContent{
    display: flex;
    flex-direction: row;
    padding:10px 0px; 
    border-bottom: 1px solid lightgreen;
    box-sizing: border-box;
    width:100%;
    position: relative;
}
.compName{
    margin-right: 20px;
    box-sizing: border-box;
    padding:10px;
    display:flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: baseline;
}
.menusNsearch{
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
    box-sizing: border-box;
    width:90%;
    position: relative;
}
.vmenusCont{
    /*display: none; */
}
.menus{
   text-align: right;
   color:white;
   margin-bottom: 40px;
   line-height: 1.5em;
   box-sizing: border-box;
   display: flex;
}
.menu{
    margin:20px;
    border-left: 1px solid white;
    padding-left: 20px;
    box-sizing: border-box;

}

.search{
   box-sizing: border-box;
   display:flex;
}
.compName{
    display: block;
}
.logo{
   
}

.mobile{
    display:none;
    height: auto;
    background-color: green;
    padding-left:20px;
    box-sizing: border-box;
    position: relative;
}
.inp{
    display: flex;
    width:80%;
    box-sizing: border-box;
    color: green;
    position: relative;
    height: 30px;
}
.inpCont{
    display: flex;
    width:100%;
    box-sizing: border-box;
    color: green;
    position: relative;
}
.menuNlogo{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:480px)
{
    .mobile{
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        width:100%;
        padding-bottom: 10px;
        border-bottom: 1px solid white;
    }
    .mainDiv{
        display: none;
    }
    .logo{
        display: flex;
        width:100%;
    }
    
      
}