
.contactFormDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:auto;
    color: white;
    font-family: 'Times New Roman', Times, serif;
    position: relative;
    background-color: green;
    padding-bottom: 30px;
    margin-top: 15px;
}
.line{
    margin-top: 5px;
    margin-bottom: 5px;
    display:flex;
    flex-direction: row;
    
    align-items: center;
}
.line img{
    margin:10px;
    border-radius: 5px;
}
.pageTitle{
    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
    font-size: 2em;
    color: burlywood;
    
}
.cformInputs{
   
    margin-top: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-top: 1px solid white;  color:green;

}
.cformInputs input,textarea,button{
    display:block;
    border:1px solid lightgreen;
    width: 100%;
    padding: 5px;   color:green;
    box-sizing: border-box;
}
.cformInputs textarea{
    height: 100px;
}
.cformInputs button{
    border-radius: 20px;
    color: white; background-color: green;
    font-weight:bold;
}
.invalidInput{
    border:1px solid red;
    background-color: salmon;
}
.hidden{
    color: red;
    display: none;
    margin:5px 0px;
    background-color: white;
    font-size: 1.5em;
}
.showError{
    display: block;
    color: red;
}
.showSuccess{
    display: block;
    color: green;
}
.showBusy{
    cursor:progress;
}