.mainDiv{
    margin:20px;
    box-sizing: content-box;
    position: relative;
    display: flex;
    flex-direction: column;
    font-family: 'Open Sans',cursive;
    padding-bottom: 10px;
}
.headSection{
   box-sizing: border-box;
   position: relative;
}

.content{
   display: flex;
   flex-direction: row;
   box-sizing: border-box;
   position: relative;
}
.category{
   display: flex;
   flex-direction: column;
   width:50%;
   justify-content: left;
   align-content: left;
   position: relative;
   box-sizing: border-box;
   
   flex-direction: column;
}
.catHeading{
   font-family: 'Tangerine', serif;
   font-size: 40px;
   color: green;
   padding-top:10px;
   font-weight: bold;
}
.hotDeals{
   width:50%;
   display: flex;
   flex-direction: column;
   justify-content: left;
   align-content: left;
   position: relative;
}
.hotDealsHeading{
   font-family: 'Tangerine', serif;
  /* font-family: 'Open Sans',cursive;*/
   font-size: 40px;
   /*font-size: 1.5em;*/
   font-weight: bold;
   color:hotpink;
   padding-top: 10px;
}
.catTitle{
   padding :5px;
   color : green;
   font-weight: 1.5em;
   width:100%;
}
.disTitle{
   padding :5px;
   color : green;
   font-weight: 1.5em;
   width: 100%;
}

.leftpane{
   display: flex;
   flex-direction: column;
   width: 100%;
}

.displayArea{
   display:flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: left;
   align-content: left;
   position: relative;
   width:100%;
   box-sizing: border-box;  
   padding-top: 5px;
}

.otherDeals{
   display:flex;
   flex-direction: row;
   flex-wrap: wrap;
   margin:7px;
   padding-top: 10px;
   border-top: 0.5px solid darkgray;
}
.odTitle{
   font-family: 'Open Sans',cursive;
   font-weight: bold;
   text-align: left;
   color: green;
   padding:5px;
   margin-top: 10px;;
}

@media screen and (max-width:480px)
{
   .content{
      flex-direction: column;
   }
   .category{
      width:100%;
   }
   .hotDeals{
      width:100%;
      justify-content: center;
      align-content: center;
   }
   .catHeading{
      font-size: 30px;
   }
}